.container{
    -webkit-animation: App-logo-spin 2s ease;
            animation: App-logo-spin 2s ease; 
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.75);
    z-index: 10;
    
    -ms-flex-align: center;
    
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    
}

#phaser{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-animation: App-logo-spin2 2s ease-in;
            animation: App-logo-spin2 2s ease-in; 
    -ms-flex-direction: column; 
        flex-direction: column;

}

@-webkit-keyframes App-logo-spin {
    from { height: 0; }
    to { height: 100%; }
  }

@keyframes App-logo-spin {
    from { height: 0; }
    to { height: 100%; }
  }
  

  @-webkit-keyframes App-logo-spin2 {
    0%{-webkit-transform: translateX(200%);transform: translateX(200%)}
    65%{-webkit-transform: translateX(-20%);transform: translateX(-20%)}
    85%{-webkit-transform: translateX(5%);transform: translateX(5%)}
    100% { -webkit-transform: translateX(0); transform: translateX(0)}
  }
  

  @keyframes App-logo-spin2 {
    0%{-webkit-transform: translateX(200%);transform: translateX(200%)}
    65%{-webkit-transform: translateX(-20%);transform: translateX(-20%)}
    85%{-webkit-transform: translateX(5%);transform: translateX(5%)}
    100% { -webkit-transform: translateX(0); transform: translateX(0)}
  }


